<template>
  <div class="container">
    <div class="pic-top-bg" :style="{'background': `url(${storeSign.storeBgImg}) 0% 0% / 100% 5.96rem no-repeat`}">
      <div class="top-bg-masking"/>
      <div class="top-gradient"/>
    </div>
    <!-- 标题栏 -->
    <div class="shop-nav-bar">
      <div class="top-back-btn" />
      <div class="search-bar" >
        <div/>
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_shop_search.png"/>
        <span>搜索</span>
      </div>
      <div class="share-btn" v-if="showShare"/>
    </div>
    <skeleton :zIndex="3" :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div v-else>
      <div class="content" id="content">
        <shop-header :shopInfo="shopBasicInfo" :storeSign="storeSign" @toogleModal="toogleModal" @updateShopInfo="getMemberStoreId"/>
        <keep-alive>
          <tab-recommend
            v-if="bottomTabType === 'tab-home'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :storeSceneData="storeSceneData"
            :storeSign="storeSign"
          />
          <tab-product
            v-if="bottomTabType === 'tab-product'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :isBottomBar="true"
          />
          <tab-category
            v-if="bottomTabType === 'tab-category'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :storeSign="storeSign"
          />
        </keep-alive>
      </div>
    </div>
    <bottom-tab
      :storeId="shopStoreId"
      :showSignUp="storeSign.storeType === 6 || storeSign.shopStoreCategory === '12'"
      :storeCode="storeSign.storeCode"
      :showSceneTab="storeSign.synStoreType!=='9'"
      :currentTab="bottomTabType"
      @changeTab="changeBottomTab"
      :storeSceneData="storeSceneData"
      v-if="!isSkeletonLoading"
    />
    <address-list-modal :storeId="shopStoreId" :showAddress.sync="showAddress"/>
  </div>
</template>
<script>
import {
  manage,
  getStoreSceneNew,
  getShopPreview,
  getStoreSignPreview
} from '@/http/service/shop'
import { mapGetters } from 'vuex'
import skeleton from '@/components/skeleton';
import shopHeader from './component/shopHeader';
import bottomTab from './component/bottom-tab/bottomTab';
import tabRecommend from './component/bottom-tab/tabRecommend';
const addressListModal = () => import('./component/addressListModal');
const tabCategory = () => import('./component/bottom-tab/tabCategory');
const tabProduct = () => import('./component/bottom-tab/tabProduct');
let ua = 'H5' // ua决定跳转链接与跳转方式
// if (utils.isSyBird()) {
//   ua = 'Flutter'
// } else if (utils.isSyBirdMP()) {
//   ua = 'WeChat'
// }

const jumpType = {
  'H5': {
    val: 5,
    urlKey: 'detailUrl'
  },
  'WeChat': {
    val: 22,
    urlKey: 'weChatDetailUrl'
  },
  'Flutter': {
    val: 16,
    urlKey: 'flutterDetailUrl'
  },
}[ua]
const localData = {
  queryStoreId: '', // route query中的storeId
  scrollTop: 0,
};
/**
 * 店铺页预览页面  商家中心用
 */
export default {
  components: {
    skeleton,
    shopHeader,
    bottomTab,
    tabRecommend,
    tabCategory,
    tabProduct,
    addressListModal,
  },
  data() {
    return {
      showShare: this.$util.isSgRn() || this.$util.isHaierApp(),
      isSkeletonLoading: false,
      bottomTabType: 'tab-home',
      shopStoreId: '', // 所属店铺Id
      shareId: '',
      shopBasicInfo: {},
      storeSign: {
        showLogo: 1,
        logoUrl: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/uplus_logo.png', // 店铺图标
        storeType: 1, //  0-非旗舰店 1-旗舰店 2-售后店 3-生态店 4-品类店 5-品旗店 6-001号店
        shopStoreCategory: '', // 店铺二级分类页面 01-乡镇店 11-智家店 12-三翼鸟店 13-综合店 31-生态店 32-家装 33-智能互联 34-安防 35-生活家电
        storeBgImg: 'http://cdn50.ehaier.com/mobilemall-admin-web/bannercontroller/image/2020/11/6858e97ff3e140acb3b004b838654e22.png', // 头部背景
        storeCode: '', // 店铺88码
        storeAddress: '', // 店铺地址
        latitude: '',
        longitude: '',
        storeName: '',
        chosenTab: 1, // 精选是否显示
      },
      storeSceneData: {},
      showAddress: false,
    };
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  created() {
    // this.storeSign.storeType = Number(this.$route.query.storeType);
    localData.queryStoreId = this.$route.query.storeId;
    // this.getShopPreview();
    this.getMemberStoreId();
  },
  activated() {
    this.$nextTick(() => {
      setTimeout(() => {
        document.getElementById('content').scrollTo(0, localData.scrollTop || 0);
      }, 100);
    });
  },
  beforeRouteLeave (to, from, next) {
    localData.scrollTop = document.getElementById('content').scrollTop;
    next();
  },
  mounted() {

  },
  methods: {
    getSceneList() {
      const param = {
        current: 1,
        size: 4,
        storeId: this.storeSign.storeId,
      };
      getStoreSceneNew(param).then(res => {
        this.loading = false;
        if (res.success) {
          let sceneUrl = ''

          if (res.data[`senceList${ua}Url`]) {
            sceneUrl = res.data[`senceList${ua}Url`]
            if (!sceneUrl.includes('?')) {
              sceneUrl += '?'
            } else {
              sceneUrl += '&'
            }
            sceneUrl += `userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
          }

          this.storeSceneData = {
            ...res.data,
            storeSenceUrl: this.$util.injectISOAddressCodeToURL(sceneUrl, this.$store.getters.getlocationInfo),
            jumpType: jumpType.val,
            sceneUrlKey: jumpType.urlKey
          }
          console.log('场景数据', this.storeSceneData)
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
    /** 获取关联的storeId */
    getMemberStoreId() {
      manage({
        storeId: localData.queryStoreId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        ionId: this.location.districtId,
        street: this.location.townshipId,
        shareStoreId: this.shareId, // 分享人id
      }).then(res => {
        if (res.success) {
          this.shopStoreId = res.data.storeId ? (res.data.storeId + '') : '';
          this.shopBasicInfo = res.data;
          this.getStoreSign();
        } else {
          this.$toast(res.message)
        }
      })
    },
    /** 获取关联的storeId */
    getStoreSign() {
      getStoreSignPreview({ storeId: this.shopStoreId }).then(res => {
        if (res.success) {
          this.storeSign = {
            ...this.storeSign,
            ...res.data,
          };
          if (res.data.synStoreType !== '9') this.getSceneList() // 店铺升级为三翼鸟店铺时才会请求场景数据
          if (!res.data.logoUrl) {
            if (Number(res.data.synStoreType) !== 9 || this.$util.isSyBird()) {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/sybird_default_logo.png`;
            } else {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/uplus_logo.png`;
            }
          }
        } else {
          console.log('获取店招模块失败');
        }
      });
    },
    getShopPreview() {
      const param = {
        // storeType: this.$route.query.storeType,
        storeCode: this.$route.query.storeCode,
      };
      getShopPreview(param).then(res => {
        if (res.success && res.data && res.data.controlViews && res.data.controlViews.length) {
          // this.shopStoreId = res.data.controlViews[0].storeId + '';
          // this.shopBasicInfo.storeId = res.data.controlViews[0].storeId + '';
          console.log('yulanstoreId=', this.shopStoreId);
        }
      });
    },
    changeBottomTab(index) {
      if (this.bottomTabType !== index) {
        this.bottomTabType = index;
      }
    },
    toogleModal(show) {
      this.showAddress = show;
    },
  },
}
</script>
